import React from "react";
import '../styles/ReportePdf.css'
import Card from 'react-bootstrap/Card';

function ReportePdf(){
    return(
        <>
            <div className="containerS">
            <Card >
                <Card.Body>
                    <Card.Title>Reporte de Gestin BIC 2021</Card.Title>
                    <Card.Text>
                    Presentamos nuestro primer reporte de gestin, base para nuestra medicin de avance en los prximos reportes.
                    </Card.Text>
                    <iframe 
                        className="reporte"
                        src={require("../Archivos/Reporte_de_Gestin_BIC_2021.pdf")}
                    >

                    </iframe>
                </Card.Body>
                </Card>
            </div>
        </>
    );
}

export {ReportePdf}